<template>
  <div ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";

export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 0,
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true,
    },
    /**
     * 
     */
    timeout: {
      type: Number,
      default: 2000,
    },
    // PullUp:{
    //   type: Boolean,
    //   dafault: true,
    // }
    pullUpLoad: {
      type: Boolean,
      dafault: false,
    },
  },
  mounted() {
    // DOMbetter-scroll
    setTimeout(() => {
      this._initScroll();
    }, 20);
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      // better-scroll
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click,
        pullUpLoad: this.pullUpLoad,
      });

      // 
      if (this.probeType == 3 || this.probeType == 2) {
        this.scroll.on("scroll", (position) => {
          this.$emit("scroll", position);
        });
      }

      //
      if (this.pullUpLoad) {
        this.scroll.on("pullingUp", () => {
          this.$emit("pullingUp");
        });
      }
    },

    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh();
    },
    finishPullUp() {
      // better-scrollrefresh
      this.scroll && this.scroll.finishPullUp();
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
  },
};
</script>